export default{
	props:["list","checknum","indexNum"],
	data(){
		return{
			val1:null,
			isShow:false
		}
	},
	created(){
		this.val1=this.$props.checknum
	},
	methods:{
		checkShow(){
			this.isShow=!this.isShow
		},
		changeVal(val){
			this.val1=val
			let data={}
			data.val=val
			data.index=this.$props.indexNum
			this.$emit("changNum",data)
		}
	}
}
